.clock {
  --degree: 0deg;
  --waves-play-state: running;
  --clock-width: 400px;
  width: var(--clock-width);
  aspect-ratio: 1;
  border: 10px solid var(--bg-color);
  background-color: var(--bg-color);
  opacity: .3;
  border-radius: 50%;
  margin: 6rem;
  transition-duration: 1s;
  position: absolute;
  scale: 1.25;
  box-shadow: -4px -4px 10px #43434380, inset 4px 4px 10px #00000080, inset -4px -4px 10px #43434380, 4px 4px 10px #0000004d;
}

.clock:before {
  content: "";
  border-radius: inherit;
  animation: 10s infinite alternate waves;
  animation-play-state: var(--waves-play-state);
  position: absolute;
  inset: 0;
  box-shadow: -4px -4px 10px #43434380, inset 4px 4px 10px #00000080, inset -4px -4px 10px #43434380, 4px 4px 10px #0000004d;
}

.clock .point {
  aspect-ratio: 1;
  filter: invert();
  background-color: #1f1c1c;
  border-radius: 50%;
  width: 4%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%)translateY(-50%);
  box-shadow: 0 0 10px 10px #ffffff40;
}

.hand {
  transform: translateX(-50%) rotateZ(var(--degree));
  filter: invert();
  position: absolute;
  bottom: 50%;
  left: 50%;
}

.hand:before {
  content: "";
  width: inherit;
  height: calc(var(--clock-width) / 10);
  background-color: inherit;
  position: absolute;
  top: 100%;
  left: 0;
  box-shadow: inset 0 0 0 4px #00000080;
}

.hand:after {
  content: attr(data-value);
  width: calc(var(--clock-width) / 20);
  aspect-ratio: 1;
  color: #fff;
  top: calc((var(--height-diff)  + var(--clock-width) / 20 + var(--value-height)) * -1);
  transform: translate(-50%, -100%) rotate(calc(var(--degree) * -1));
  font-size: calc(var(--clock-width) / 20);
  text-align: center;
  filter: invert();
  background-color: #0000;
  font-family: Orbitron, sans-serif;
  position: absolute;
}

.hand.second {
